import "./Header.css";
import {
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  IconButton,
  TableCell,
  TableRow,
  TextField,
  Box,
  Select,
  MenuItem,
} from '@mui/material';
import React, { useRef, useState } from 'react';
import {
  AccountCircle,
} from '@mui/icons-material';

function Header() {
  const [isDialogOpen, setIsDialogOpen] = useState(true);
  const [weight, setWeight] = useState("");
  const [age, setAge] = useState("");
  const [sex, setSex] = useState("");
  const inputRef = useRef(null);
  const imageUpload = () => {
    console.log(inputRef.current);
    inputRef.current?.click();
  };
  const [isSign,setIsSign] = useState(false);
  return (
    <header>
      <h1 className="service-title">regretter</h1>
      <Box className="iconBox">
      <Button
        onClick={() => setIsDialogOpen(true)}
      >
          <AccountCircle className="user-icon"/>
      </Button>
      </Box>
      <Dialog open={isDialogOpen} fullWidth maxWidth="md"
        PaperProps={{
          style: {
            boxShadow: "none",
            textAlign: "center",
            width:'auto',
          },
        }}>
        <DialogTitle>{!isSign?'':''}</DialogTitle>
        <DialogContent>
          <TableRow>
            <TableCell>
              <input hidden ref={inputRef} type="file" accept="image/*" />
              <IconButton onClick={imageUpload}>
                <AccountCircle />
              </IconButton>
            </TableCell>
            <TableCell></TableCell>
          </TableRow>
          <TableRow>
          <TableCell></TableCell>
          <TableCell>
            <Select value={sex} onChange={(e)=>setSex(e.target.value)}>
              <MenuItem value=''>
              
              </MenuItem>
              <MenuItem value=''>
              
              </MenuItem>
            </Select>
          </TableCell>
          </TableRow>
          <TableRow>
            <TableCell></TableCell>
            <TableCell>
              <TextField
                onChange={(e) => setWeight(e.target.value)}
                value={weight}
              />
            </TableCell>
          </TableRow>
          <TableRow>
            <TableCell ></TableCell>
            <TableCell>
              <TextField onChange={(e) => setAge(e.target.value)} value={age} />
            </TableCell>
          </TableRow>
          <TableRow >
            <TableCell>
              <Button onClick={() => setIsDialogOpen(false)}></Button>
            </TableCell>
            <TableCell>
              <Button disabled={(age === ''||weight===''||sex==='')}onClick={() => {setIsDialogOpen(false);setIsSign(true)}}></Button>
            </TableCell>
          </TableRow>
        </DialogContent>
      </Dialog>
    </header>
  );
}

export default Header;